body {
  background: black;
  margin: 0;
}

main {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 293px;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: black;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, black, rgb(20, 20, 20));
  background-size: 100% 100%;
  background-position: 100% 100%;
  border-radius: 0 0 32px 32px;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

@media only screen and (max-width: 500px) {
  main {
    min-height: 100vh;
    padding-bottom: 270px;
  }
}
@font-face {
  font-family: "StripesNation";
  src: url("../assets/fonts/StripesNation.ttf");
  font-weight: normal;
  font-style: normal;
}
* {
  color: white;
}

h1, h2, h3 {
  font-family: "StripesNation";
  font-weight: normal;
}

h1 {
  font-size: 64px;
}

h2 {
  font-size: 36px;
  margin: 18px 0;
}

h3 {
  font-size: 24px;
}

p, a, button {
  font-size: 18px;
  font-family: "Poppins";
}

p {
  line-height: 1.5;
}

a {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  position: relative;
  transition: 0.2s ease-in-out;
}
a .link-border {
  border: 2px solid white;
}
a.text-link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid rgba(255, 255, 255, 0.4);
  z-index: -1;
  scale: 0.4 0.9;
  transition: 0.2s ease-in-out;
}
a:hover::before {
  scale: 0.9 0.9;
  border-color: #ed1c24;
}

@media only screen and (max-width: 500px) {
  h1 {
    font-size: 48px;
  }
  h2 {
    font-size: 32px;
    margin: 18px 0;
  }
  h3 {
    font-size: 24px;
  }
  p, a, button {
    font-size: 16px;
  }
}
.sn-red {
  fill: #ed1c24;
}

.icon {
  width: 48px;
}

.wordmark {
  height: 48px;
}

.icon-stroke {
  fill: #fff;
  stroke-width: 0px;
}

.icon-border {
  fill: transparent;
  transition: 0.2s ease-in-out;
}

.icon-link {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: 0.2s ease-in-out;
}

.icon-link:hover {
  scale: 0.9;
}
.icon-link:hover .icon-border {
  fill: white;
}

@keyframes ani-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}
nav .nav-text {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 0 10px;
  height: 24px;
  width: -moz-fit-content;
  width: fit-content;
  transition: 0.2s ease-in-out;
}
nav .nav-text p {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "StripesNation";
  font-size: 24px;
  padding-left: 1px;
  padding-top: 3px;
  height: 36px;
}
nav .nav-text .link-bgr-wrap, nav .nav-text .link-bgl-wrap {
  position: absolute;
  overflow: hidden;
  width: 16px;
  height: 16px;
}
nav .nav-text .link-bgr-wrap {
  top: 0px;
  right: -1px;
}
nav .nav-text .link-bgr-wrap > div {
  top: 0;
  right: 3px;
}
nav .nav-text .link-bgl-wrap {
  bottom: -4px;
  left: -1px;
}
nav .nav-text .link-bgl-wrap > div {
  bottom: 0;
  left: 3px;
}
nav .nav-text .link-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 3.5px solid white;
  transition: 0.2s ease-in-out;
  transform: skew(-15deg);
  z-index: -1;
  scale: 0;
}
nav .nav-text .link-active {
  position: absolute;
  top: 2px;
  width: 50%;
  height: 100%;
  border-bottom: 3px solid red;
  transition: 0.2s ease-in-out;
  transform: skew(-15deg);
  z-index: -2;
  scale: 0 1;
}
nav .nav-text:hover {
  scale: 0.92;
}
nav .nav-text:hover .link-bg {
  scale: 1;
}

.button {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 4px 16px;
  border: 3px solid transparent;
  transition: 0.2s ease-in-out;
}
.button p {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: 0.2s ease-in-out;
  font-weight: 500;
}
.button .btn-bgr-wrap, .button .btn-bgl-wrap {
  position: absolute;
  overflow: hidden;
  width: 16px;
  height: 16px;
  opacity: 0;
}
.button .btn-bgr-wrap {
  top: 0;
  right: 0;
  transition: 0.2s ease-in-out;
}
.button .btn-bgr-wrap > div {
  top: 0;
  right: 0;
}
.button .btn-bgl-wrap {
  bottom: 0;
  left: 0;
  transition: 0.2s ease-in-out;
}
.button .btn-bgl-wrap > div {
  bottom: 0;
  left: 0;
}
.button .btn-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 3px solid white;
  transition: 0.2s ease-in-out;
  z-index: -1;
  scale: 1;
}
.button:hover p {
  scale: 0.95;
}
.button:hover .btn-bgr-wrap, .button:hover .btn-bgl-wrap {
  opacity: 100%;
}
.button:hover .btn-bgr-wrap {
  top: 2px;
  right: 6px;
}
.button:hover .btn-bgl-wrap {
  bottom: 2px;
  left: 6px;
}
.button:hover.primary {
  background: transparent;
}
.button:hover.primary p {
  color: white;
}
.button:hover.secondary {
  border-color: transparent;
}
.button:hover.tertiary {
  border-color: transparent;
  -webkit-backdrop-filter: blur(0px);
          backdrop-filter: blur(0px);
}
.button.primary {
  background: white;
}
.button.primary p {
  color: black;
}
.button.secondary {
  border-color: white;
}
.button.tertiary {
  border-color: white;
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}

nav {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 64px;
  position: fixed;
  top: 0;
  left: 0;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%);
  overflow: hidden;
  z-index: 999;
}
nav > div {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding: 0 80px;
  max-width: 1280px;
  width: 100%;
  gap: 24px;
}
nav > div .nav-left {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  grid-column: 1/4;
  grid-row: 1;
  width: 100%;
  height: 64px;
}
nav > div .nav-mid {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  grid-column: 4/10;
  grid-row: 1;
  width: 100%;
  height: 64px;
  gap: 48px;
}
nav > div .nav-mid .gallery, nav > div .nav-mid .about, nav > div .nav-mid .connect {
  transform: translateY(-100%);
  opacity: 0;
}
nav > div .nav-mid .gallery {
  animation: nav-down 1.5s 1.5s ease-in-out forwards;
}
nav > div .nav-mid .about {
  animation: nav-down 1.5s 1.7s ease-in-out forwards;
}
nav > div .nav-mid .connect {
  animation: nav-down 1.5s 1.9s ease-in-out forwards;
}
nav > div .nav-right {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  grid-column: 10/13;
  grid-row: 1;
  width: 100%;
  height: 64px;
  gap: 16px;
}
nav > div .nav-right .instagram, nav > div .nav-right .facebook, nav > div .nav-right .linkedin {
  transform: translateY(100%);
  opacity: 0;
}
nav > div .nav-right .instagram {
  animation: nav-up 1.5s 1.5s ease-in-out forwards;
}
nav > div .nav-right .facebook {
  animation: nav-up 1.5s 1.7s ease-in-out forwards;
}
nav > div .nav-right .linkedin {
  animation: nav-up 1.5s 1.9s ease-in-out forwards;
}
nav > div .nav-burger {
  display: none;
}

@media only screen and (max-width: 500px) {
  nav {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: center;
    align-items: start;
    overflow: hidden;
    transition: 0.5s ease-in-out;
  }
  nav > div {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 60vh;
  }
  nav > div .nav-left {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    position: absolute;
    top: 0;
    left: 16px;
    width: 64px;
    height: 64px;
  }
  nav > div .nav-mid {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 64px;
    gap: 32px;
  }
  nav > div .nav-mid .gallery, nav > div .nav-mid .about, nav > div .nav-mid .connect {
    transform: translateY(0%);
    opacity: 100%;
    animation: none;
  }
  nav > div .nav-right {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 64px;
    margin-top: 32px;
    gap: 16px;
  }
  nav > div .nav-right .instagram, nav > div .nav-right .facebook, nav > div .nav-right .linkedin {
    transform: translateY(0%);
    opacity: 100%;
    animation: none;
  }
  nav > div .nav-burger {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: end;
    align-items: center;
    position: absolute;
    display: flex;
    top: 0;
    right: 16px;
    width: 64px;
    height: 64px;
  }
  nav > div .nav-burger svg {
    position: absolute;
    margin-right: -8px;
    width: 48px;
    transition: 0.2s ease-in-out;
  }
  nav > div .show {
    opacity: 100%;
  }
  nav > div .hide {
    opacity: 0;
  }
  .menu-open {
    height: 100vh;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
  }
}
@keyframes nav-down {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 100%;
  }
}
@keyframes nav-up {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 100%;
  }
}
