.home {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding: 0 80px;
  max-width: 1280px;
  width: 100%;
  gap: 24px;
}
.home .img-wrap {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  width: 100%;
  height: 120vh;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden;
  opacity: 0;
  animation: ani-opacity 3s 0.5s ease-in-out forwards;
  z-index: -1;
}
.home .img-wrap img {
  margin-top: -5vh;
  max-height: 125vh;
  max-width: 100%;
  aspect-ratio: 3/2;
  z-index: -1;
}
.home .img-wrap .parallax-front {
  position: absolute;
  animation: parallax-front 3s 0.5s ease-out;
}
.home .img-wrap .parallax-front .parallax-gif {
  position: absolute;
  z-index: 2;
}
.home .img-wrap .parallax-back {
  z-index: -2;
  animation: parallax-back 3s 0.5s ease-out;
}
.home .img-wrap .parallax-back .img-overlay {
  max-width: 100%;
  height: 125vh;
  position: absolute;
  background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 33%, rgba(0, 0, 0, 0) 66%, rgb(0, 0, 0) 100%);
  aspect-ratio: 3/2;
}
.home .img-wrap .mobile-img-overlay {
  display: none;
}
.home article {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-column: 1/5;
  margin-top: 10vh;
  height: 100%;
  z-index: 1;
  opacity: 0;
  background: radial-gradient(circle, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 0) 100%);
  animation: ani-opacity 1.2s 2s ease-in-out forwards;
}
.home article h1 {
  font-size: 40px;
  line-height: 0.8;
  margin: 32px 0;
}
.home article h1 span {
  font-size: 80px;
}
.home article p {
  margin-top: 0;
}
.home article > div {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  width: 100%;
  gap: 16px;
}

@keyframes parallax-front {
  0% {
    transform: translateX(-3%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes parallax-back {
  0% {
    transform: translateX(3%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes parallax-load {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}
